 
import React from "react"
import { Link, ModalRoutingContext } from 'gatsby-plugin-modal-routing'

import { Close } from '@styled-icons/material/Close'

import Layout from '../Layout'

import * as S from './styled'

const ModalIdeia = ({ children, ...rest }) => (
  <ModalRoutingContext.Consumer className="xablau">
    {({ modal, closeTo }) => (
      modal ? (
        <React.Fragment>
          <Link to={closeTo}>
            <S.ModalClose>
              <Close />
            </S.ModalClose>
          </Link>
          {children}
        </React.Fragment>
      ) : (
        <Layout { ...rest }>
          {children}
        </Layout>
      )
    )}
  </ModalRoutingContext.Consumer>
)
export default ModalIdeia